        const dingle = `Template#${this.ran()}#${this.ran()}`;
        await DataStore.save(
          TemplatePool.copyOf(original, updated => {
            updated.TL = this.msg.TL || `This is a new Title ${this.ran()}`;
            updated.CXSK = dingle;
            updated.TLOC = `${original.PK}&${dingle}`;
          })
        ).then(async () => await this.loadTodos());
        // this.msg = {};


        // await DataStore.delete(TemplatePool, id)
        //   .then(async () => await this.loadTodos())
        //   .catch(e => console.log(`Error deleting ${id} todos: ${e}.`));
      }

  },
