     await DataStore.delete(TemplatePool, id)
       .then(() => this.loadTodos())
       .catch(e => console.log(`Error deleting ${id} todos: ${e}.`));
   },
   async deleteAll() {
     await DataStore.delete(TemplatePool, Predicates.ALL)
       .then(() => this.loadTodos())
       .catch(e => console.log(`Error deleting all todos: ${e}.`));
   },
